*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
.init-nav{
  display: flex;
  align-items: center;
  height: 70px;
  background-color: #1D1E18;
}
.init-nav-left p{
  margin-left: 30px;
  color: #fff;
  opacity: 0.9;
  letter-spacing: 1px;
  font-family: "Gemunu Libre", sans-serif;
  font-size: 1.6rem;
  cursor: pointer;
}
.init-nav-right{
  margin-left: 30%;
}
.init-indicate{
  height: 40px;
  width: 100%;
  background-color: #343434;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  margin-left: 70px;
}
.box{
  height: 15px;
  width: 15px;
  background-color: #e63464;
}
.box1{
  background-color:  turquoise;
}
.box2{
  background-color: #F46036;
}
.box3{
  background-color: #FCBA04;
}
.init-indicate-items{
  padding:0 40px 0 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 14px;
  letter-spacing: 1px;
  opacity: 0.7;
}
.init-indicate-items p{
  margin-left: 10px;
}
/* 
Component Mergesort css Start here 
*/
.adjust{
  align-items: baseline;
  display: flex;
  justify-content: center;
  height: 70vh;
  background-color: #343434;
}
.init-array-bar{
  width: 4px;
  background-color: #4CAF50;
  display: inline-block;
  margin: 90px 2px;

}
.init-buttons{
  height: 10vh;
  background-color: #414141;
}