*{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
    box-sizing: border-box;
}
.array-container{
    background-color: #242424;
    text-align: center;
    height: 90vh;
}
.array-bar{
    width: 4px;
    display: inline-block;
    margin: 4% 2px 2% 2px;
}
.buttonsclick{
   background-color: #414141;
   border: none;
   padding: 10px 20px;
   margin: 2% 1%;
   font-size: 16px;
   color: #fff;
   font-family: Arial, Helvetica, sans-serif;
   font-weight: 500;


}